import { SET_ALERT, REMOVE_ALERT } from '../actions/types';

const initialState = [];

export default function (state = initialState, action) {
  const { type, payload } = action;

  switch (type) {
    // Dispatch alert type
    case SET_ALERT:
      // return array with payload
      return [...state, payload];
    case REMOVE_ALERT:
      // Filters through and returns all alerts that do not match payload
      return state.filter((alert) => alert.id !== payload);
    default:
      return state;
  }
}
